import React from "react";

class Editor extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(e) {
    this.props.onInputChange(e.target.value);
  }
  render() {
    const isVerticalOnTablet = this.props.isVerticalOnTablet;
    return (
      <div className={`column ${isVerticalOnTablet ? "is-half" : "is-full"}`}>
        <div className="field">
          <label className="label">Markdown</label>
          <div className="control">
            <textarea
              className={`textarea ${
                isVerticalOnTablet ? "is-tablet-vertical" : ""
              }`}
              placeholder="Enter markdown here..."
              id="editor"
              onChange={this.handleChange}
              defaultValue={this.props.defaultTextValue}
            ></textarea>
          </div>
        </div>
      </div>
    );
  }
}

export default Editor;
